import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
		console.log(pathname)
		if(pathname=='/Search' || pathname=='/Jiubei') {
		    return null; 
		}
        return (
            <div style={ {   
				position: 'fixed',  
				width: '100%',  
				bottom: 0 } }
			>
            <TabBar 
				unselectedTintColor="#949494" 
				tintColor="red" 
				barTintColor="white" 
			>
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye1'></i>                           
                        }
                        selected={ pathname === '/Shouye' }
                        onPress={ () => {
                            this.props.history.push('/Shouye');
                        } }
                    > </TabBar.Item>
					
					
					
                    <TabBar.Item
						title="购物车"
						key="cart"
                        icon={
                            <i className='iconfont icon-gouwuche'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwuche1'></i>                           
                        }
                        
                        selected={ pathname === '/Cart' }
                        onPress={ () => {
                            this.props.history.push('/Cart')
                        } }
                    ></TabBar.Item>
					
					
					
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-xiaoxi'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-xiaoxi1'></i>                           
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ pathname === '/Dingdan' }
                        onPress={ () => {
                            this.props.history.push('/Dingdan')
                        } }
                    ></TabBar.Item>
					
					
					
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode'></i> }
						selectedIcon={
						    <i className='iconfont icon-wodedangxuan'></i>                           
						}
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/My' }
                        onPress={ () => {
                            this.props.history.push('/My')
                        } }
                    ></TabBar.Item>			
				</TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);