import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Mytab from './Mytab'
import Dingdan from './Dingdan'
import Cart from './Cart'
import My from './My'
import Shouye from './Shouye'
import Search from './child/Search'
import Jiubei from './Jiubei.js'
import Boxcommodity from './box_commodity'
const App = () => {
	return(
		<Router>
			<Switch>
				<Route path='/Shouye' component={Shouye} />
				<Route path='/Cart' component={Cart} />
				<Route path='/Dingdan' component={Dingdan} />
				<Route path='/My' component={My} />
				<Route path='/Search' component={Search} />
				<Route path='/Jiubei' component={Jiubei} />
				<Route path='/Boxcommodity' component={Boxcommodity} />
				<Route render={()=><Redirect to='/Shouye'/>} />
			</Switch>
			
			<Mytab />
		</Router>)
}
export default App;